}
header ul li .downloadCV {
  color: #f9fafb;
  padding: 0.5vw 1vw;
  border-radius: 12px;
  background-color: #111827;
  transition: 0.4s ease;
}
header ul li .downloadCV:hover {
  opacity: 0.7;
  color: #f9fafb;
}
header ul li:nth-child(4) {
  border-right: #f3f4f6;
}

