  text-align: center;
}
.offScreenMenu ul {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  border-block: 1px solid #e5e7eb;
  gap: 2.5vw;
}
.offScreenMenu ul li {
  font-size: 1.025rem;
  list-style-type: none;
}
.offScreenMenu ul li a {
  color: #4b5563;
  text-decoration: none;
  transition: 0.4s ease;
}
.offScreenMenu ul li a:hover {
  color: #111827;
}
.offScreenMenu ul li:nth-child(4) {
  border-right: #f3f4f6;
}
.offScreenMenu .downloadCV {
  color: #f9fafb;
  padding: 0.5vw 1vw;
  border-radius: 12px;
  font-size: 1.025rem;
  font-weight: 500;
  text-align: center;
  background-color: #111827;
  transition: 0.4s ease;
}
.offScreenMenu .downloadCV:hover {
  opacity: 0.7;
  color: #f9fafb;
}
.offScreenMenu span {
  display: flex;
  align-items: center;
  color: #4b5563;
  margin-block: 1vw;
  justify-content: space-between;
  font-size: 1.025rem;
}

.offScreenMenu.active {
  display: flex;
  animation: 0.4s offScreenActive;
  right: 0;
}

@keyframes offScreenActive {
  from {
    right: -40vw;
  }
  to {
    right: 0;
  }
}
.firstContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5vw;
  padding: 5vw;
  padding-top: 8vw;
}
.firstContainer .firstTextContainer {
  display: flex;
  flex-direction: column;
}
.firstContainer .firstTextContainer .firstDescription {
  padding-block: 2vw;
  max-width: 50vw;
  font-size: 1.025rem;
  color: #4b5563;
}
.firstContainer .firstTextContainer span {
  display: grid;
  grid-template-columns: 2vw 18vw;
  grid-template-rows: repeat(2, auto);
  justify-items: center;
  align-items: center;
  gap: 0.1vw;
  font-size: 1.025rem;
  color: #4b5563;
}
.firstContainer .firstTextContainer span p {
  width: 18vw;
}
