.fifthContainer p {
  font-size: 20px;
  color: #4b5563;
}
.fifthContainer ul {
  display: flex;
  gap: 2vw;
  flex-direction: column;
}
.fifthContainer ul li {
  box-shadow: 0 4px 3px rgba(0, 0, 0, 0.07);
