  grid-template-columns: 20vw;
  align-items: center;
  justify-items: center;
  box-shadow: 0 4px 3px rgba(0, 0, 0, 0.07);
  border-radius: 12px;
}
.sixthContainer ul li p {
  font-size: 1.025rem;
}

