.seventhContainer .popup .popuptext {
  visibility: hidden;
  width: 10vw;
  background-color: #555;
  color: #fff;
  text-align: center;
  border-radius: 3vw;
  padding: 8px 0;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  font-size: 14px;
  left: 50%;
  margin-left: -80px;
}
.seventhContainer .popup .popuptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}
.seventhContainer .popup .show {
  visibility: visible;
  animation: fadeIn 0.4s;
}
